@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  font-family: "Inter", sans-serif;
}

img {
  width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  all: unset;
  cursor: pointer;
}

button {
  font-size: medium;
  font-weight: 500;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
}

a {
  color: #FFFFFF;
}
a:hover {
  color: #FFE492;
}

section {
  margin-top: 6.25rem;
  margin-bottom: 6.25rem;
}

h1 {
  line-height: 1.2;
  max-width: 16ch;
  font-size: clamp(2.25rem, 2.5vw + 1.5rem, 4rem);
}

.flex-between-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  max-width: 90rem;
  margin-inline: auto;
  padding-inline: 2rem;
}

.hero {
  color: #4F9CF9;
}
.hero__wrapper {
  display: flex; /* Flexbox for inner content */
  flex-direction: column;
  align-items: stretch; /* Ensures both containers stretch to equal height */
  width: 100%;
}
@media (min-width: 48.0625em) {
  .hero__wrapper {
    flex-direction: row;
  }
}
.hero__content {
  flex: 1; /* Allows this section to take up equal or remaining space */
  display: flex; /* Flexbox inside content for proper alignment */
  flex-direction: column; /* Ensures children are stacked */
  justify-content: space-between; /* Vertically centers the text inside the left column */
  align-items: center;
  text-align: center;
  gap: 1.5rem;
}
@media (min-width: 48.0625em) {
  .hero__content {
    text-align: left;
    align-items: flex-start;
    justify-content: space-around;
  }
}
.hero__text {
  max-width: 60ch;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 0.5rem;
}
@media (min-width: 48.0625em) {
  .hero__text {
    align-items: flex-start;
  }
}
.hero img {
  padding: 1.5rem;
  flex: 1; /* Allows the image to take up space equally with hero__content */
  max-width: 100%; /* Ensures the image doesn't overflow */
  height: auto; /* Maintains aspect ratio */
}
@media (min-width: 48.0625em) {
  .hero img {
    padding: 0;
  }
}

.navbar {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__links {
  display: none;
  color: #4F9CF9;
}
@media (min-width: 79em) {
  .navbar__links {
    display: block;
  }
  .navbar__links li {
    display: inline-block;
    position: relative;
    margin-right: 2rem;
  }
}
.navbar__links .navbar__link {
  position: relative;
}
.navbar__links .navbar__link:after {
  content: "";
  display: inline-block;
  background: url("../icons/down-arrow.svg") no-repeat center;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 0.15rem;
  position: relative;
}
.navbar__dropdown {
  position: absolute;
  display: none;
  z-index: 1;
  background-color: #4F9CF9;
}
.navbar__dropdown--show {
  display: block;
}
.navbar__dropdown a {
  padding: 0.5rem 0.5rem;
  display: block;
}
.navbar__buttons {
  display: flex;
  align-items: center;
}
.navbar__buttons button {
  margin-inline: 0.625rem;
}
.navbar__cta {
  display: none;
}
@media (min-width: 48.0625em) {
  .navbar__cta {
    display: block;
  }
}
@media (min-width: 79em) {
  .navbar__hamburger-menu--disable {
    display: none;
  }
}

.btn-primary {
  background-color: #FFE492;
  padding: 1rem 2.5rem;
  color: #043873;
}

.btn-secondary {
  background-color: #4F9CF9;
  color: #FFFFFF;
  padding: 1rem 1.5rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.btn-secondary:after {
  content: "→";
  margin-left: 0.5rem;
  font-size: 1rem;
  color: #FFFFFF;
}